
// Navigation
let home = document.querySelector(".home")
let history = document.querySelector(".history");
let service = document.querySelector(".service");
let gallery = document.querySelector(".gallery");
let link = document.getElementsByClassName("header__item");

function removeHeader() {
    menu.classList.remove("_active");
    burger.classList.remove("_active");
    body.classList.remove("_lock");
}

function offset(el) {
    const rect = el.getBoundingClientRect(),
        scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
        scrollTop = window.pageYOffset || document.documentElement.scrollTop;
    return { top: rect.top + scrollTop, left: rect.left + scrollLeft }    
}


for (let i = 0; i < link.length; i++) {
    link[i].addEventListener("click", function() {
        let number = this.dataset.number;
        if (number == 1) {
            window.scrollTo({top: offset(home).top, behavior:"smooth"})
            removeHeader();
        } else if (number == 2) {
            window.scrollTo({top: offset(history).top, behavior:"smooth" })
            removeHeader();
        } else if (number == 3) {
            window.scrollTo({top: offset(service).top, behavior:"smooth"})
            removeHeader();
        } else if (number == 4) {
            window.scrollTo({top: offset(gallery).top, behavior:"smooth"})
            removeHeader();
        }
    })
}

// Menu burger
let body = document.querySelector(".body");
let burger = document.querySelector(".header__burger");
let menu = document.querySelector(".header__menu");

burger.addEventListener("click", function() {
    menu.classList.toggle("_active");
    burger.classList.toggle("_active");
    body.classList.toggle("_lock");
})



